* {
  box-sizing: border-box;
  margin: 0;
  padding-top: 0;
}
.navbar * {
  color:rgb(149, 24, 24);
}
.none{visibility: hidden;}
.container {
  display: flex;
  border: 2px solid rgb(195, 239, 248);
  border-radius: 5px;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-between;
  height: 50px;
  /* position: absolu */
  margin: 0 auto;
  background-color: rgb(0, 126, 94);
}
input {
  height: 30px;
  line-height: 50px;
  text-align: center;
  padding: 7px;
}

.icons {
  /* margin-right: 16%; */
  display: inline-block;
  margin-right: 17%;
 
}
.navbar .bi {
  display: inline-block;
  font-size: 1.8rem;
  padding-right: 10px;
}

.story-container {
  border-bottom: 2px solid rgb(252, 174, 255);
  display: flex;
  /* padding-top:40px; ; */
  width: 600px;
  margin-left: 10%;

 
}
.post-options .bi {
  font-size: 1.3rem;
  /* margin-right: 7px; */
}
#story {
  text-align: center;
}
.story-container {
  overflow: scroll;
}
.story-container::-webkit-scrollbar {
  width: 0
}
#story > .img {
  display: inline-block;

  border-radius: 50%;
  width: 65px;
  height: 65px;
  margin: 8px;
  margin-bottom: 0;
  /* border: 1px solid rgb(230, 118, 118); */
}
.post-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid rgb(228, 223, 223);
  width: 100%;
  padding: 5px;
  margin-top:10px;
  margin-bottom: 9px;
}

.post-nav > input{border:none}
.post-container {
  margin-top: 2%;
  margin-left: 10%;
  width: 600px;
  margin-top: 2%;
  margin-bottom: 20px;
}
.post-containers {
  background-color: rgb(248, 248, 248);
  margin-top: 2%;
  padding: 2%;
  box-shadow: 0px 0px 9px 0px #5d5b5b  ;
  border-radius: 8px;
  height: 610px; 
}
.cmt-container{
  opacity: .9;
  font-weight: 700;
}
.postImage {
  margin: 0;
  width: 100%;
  height: 60%;
  border: 1px solid rgb(255, 247, 247);
  padding: 2px;
}
.post-options,
.post-user {
  display: flex;
  align-items: center;
  padding:0 1%;
  height: 35px;
  
  justify-content: space-between;
  border: 1px solid rgb(202, 206, 205);
  border-radius: 3px;
}
.bi {
  font-weight: 700;
  color: black;
  margin-bottom: 4%;
}
.bi-chevron-right{
  left: calc(7% + 600px);
}
.story-img {
  border-radius: 50%;
  border: 2px solid rgb(230, 28, 105);
  width: 67px;
  height: 67px;
}
.navbar {
  position: sticky;
  top:0%;
  margin-top: 10px;
  width: 100vw;
  /* background-color: rgb(87, 87, 230); */
  /* margin-left: 10%; */
  opacity: 0.9;
}
@media screen and (max-width: 600px) {
  .post-container {
    margin: 0;
    width: 100%;
  }
  input{padding:0;}
  .story-container{
    margin: 0;
  }
  .bi-chevron-right{
    left:90%;
  }
  .bi-chevron-left{
    left:6%;
  }
}
.suggested-userProfile {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2%;
  
  padding: 0 0.5%;
}
.userProfile .bi {
  font-size: 3rem;
  margin-right: 1%;
}
.user-detail {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.link {
  color: rgb(59, 135, 250);
  font-weight: 300;
}
.suggested-userProfile .bi {
  font-size: 2rem;
}
.profile-container{
  width: 400px;
  
  position:fixed;
  left:calc(610px + 10%);
  top:17%;
  padding:0 3%;
}
.suggetions-img{
  border-radius: 50%;
  margin:5px;
  border:  2px solid rgb(243, 38, 89);
}
.post-options span .bi{
  margin: 10px;;
}
.bi-chevron-right ,.bi-chevron-left{
  position: absolute;
  margin-top:34px;
  font-weight: 1.3rem;
  color:rgb(83, 3, 97);
  font-weight: 900;
  font-size: 1.3;
  width: 23px;
  text-align: center;
  background-color: white;
  border-radius: 50%;
}
.bi-chevron-left{left:11%;}
.profile-imgs{
  width: 50px;
  height: 50px;
  border-radius: 50%;
  padding-right: 2%;
}
.more-btn{
  margin:8% auto;
  padding:10px 9%;
  background-color: rgb(22 138 214);
  color: white;
  border-color: white;
  font-size: 1.1rem;
  font-weight: 600;
  border-radius: 7px;
  box-shadow: 6px 6px 6px -1px rgba(63, 59, 59, 0.87);
  transition: all .5s ease-in-out;
}
.more-btn:hover{
  transform: scale(1.05);
}
@media screen and (max-width:480px)
{
  .navbar-input{
    display: none;
  }
  .bi-chevron-left,.bi-chevron-right{
    
      display: none;;
    }
    .icons{display: none;}
    .container img{
      margin: 0 auto;
    }
    
  
  .post-containers{
    margin:5% 10px;
    width: calc(100%-30px);
    border-radius: 4px;
    
  }
  body::-webkit-scrollbar{
    width: 0;
  }
  .story-container{
    width: 100%;
  }
  
}
.colored{
  color: rgb(236, 20, 20);
}